module.exports = "/* GLOBAL STYLES\r\n-------------------------------------------------- */\n/* Padding below the footer and lighter body text */\nbody {\n  padding-top: 3rem;\n  padding-bottom: 3rem;\n  color: #5a5a5a; }\n/* CUSTOMIZE THE CAROUSEL\r\n  -------------------------------------------------- */\n/* Carousel base class */\n.carousel {\n  margin-bottom: 4rem; }\n/* Since positioning the image, we need to help out the caption */\n.carousel-caption {\n  bottom: 3rem;\n  z-index: 10; }\n/* Declare heights because of positioning of img element */\n.carousel-item {\n  height: 42rem;\n  background-color: #777; }\n.carousel-item > img {\n  position: absolute;\n  top: 0;\n  left: 0;\n  min-width: 100%;\n  height: 42rem; }\n/* MARKETING CONTENT\r\n  -------------------------------------------------- */\n/* Center align the text within the three columns below the carousel */\n.marketing .col-lg-4 {\n  margin-bottom: 1.5rem;\n  text-align: center; }\n.marketing h2 {\n  font-weight: 400; }\n.marketing .col-lg-4 p {\n  margin-right: .75rem;\n  margin-left: .75rem; }\n/* Featurettes\r\n  ------------------------- */\n.featurette-divider {\n  margin: 5rem 0;\n  /* Space out the Bootstrap <hr> more */ }\n/* Thin out the marketing headings */\n.featurette-heading {\n  font-weight: 300;\n  line-height: 1;\n  letter-spacing: -.05rem; }\n/* RESPONSIVE CSS\r\n  -------------------------------------------------- */\n@media (min-width: 40em) {\n  /* Bump up size of carousel content */\n  .carousel-caption p {\n    margin-bottom: 1.25rem;\n    font-size: 1.25rem;\n    line-height: 1.4; }\n  .featurette-heading {\n    font-size: 50px; } }\n@media (min-width: 62em) {\n  .featurette-heading {\n    margin-top: 7rem; } }\n\r\n/*# sourceMappingURL=data:application/json;base64, */"