import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import {FormsModule} from '@angular/forms';
import {RouterModule, Routes} from '@angular/router' // import
import {HttpModule} from '@angular/http';

import { MDBBootstrapModule } from 'angular-bootstrap-md';

import { HomeComponent } from './component/home/home.component';
import { LoginComponent } from './component/login/login.component';
import { RegisterComponent } from './component/register/register.component';
import {ValidateService} from'./Services/validate.service';
import {AuthService} from'./Services/auth.service';
import {AuthGuard} from './guards/auth.guard';
import { DashbordComponent } from './component/dashbord/dashbord.component';
import { ProfileComponent } from './component/profile/profile.component';
import { NavbarComponent } from './component/navbar/navbar.component';
import { RegisterStudentComponent } from './component/register-student/register-student.component';

const appRoutes:Routes = [ //type
  {path:'',component:HomeComponent},
  {path:'register',component:RegisterComponent},
  {path:'register-student',component:RegisterStudentComponent},
  {path:'login',component:LoginComponent},
  {path:'dashbord',component:DashbordComponent, canActivate : [AuthGuard]},
  {path:'profile',component:ProfileComponent, canActivate : [AuthGuard]},
];


@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    LoginComponent,
    RegisterComponent,
    DashbordComponent,
    ProfileComponent,
    NavbarComponent,
    RegisterStudentComponent
  ],
  imports: [
    HttpModule,
    BrowserModule,
    AppRoutingModule,
    MDBBootstrapModule.forRoot(),
    FormsModule,
    RouterModule.forRoot(appRoutes)
  ],
  providers: [ValidateService,AuthService, AuthGuard],
  bootstrap: [AppComponent]
})
export class AppModule { }
